import classes from './Log.module.css'
import Card from '../UI/Card/Card'
import { useEffect, useState } from 'react'
import ModalOverlay from '../Modal/ModalOverlay'
import TableRow from './TableRow'
import DetailModal from './DetailModal'
import FeatureBackdrop from '../Modal/FeatureBackdrop'

const Log = (props) => {
  const [data, setData] = useState([])
  const [isLogEmpty, setIsLogEmpty] = useState(true)
  const [filterType, setFilterType] = useState('All')
  const [filterMonth, setFilterMonth] = useState(0)
  const [deleteModal, setDeleteModal] = useState(false)
  const [selectedDeleteItem, setSelectedDeleteItem] = useState({})
  const [deleteSuccess, setDeleteSuccess] = useState(false)
  const [detailModal, setDetailModal] = useState(false)
  const [detailProp, setDetailProp] = useState({})

  const onSetSelectedDeleteItem = (selectedItem) => { setSelectedDeleteItem(selectedItem) }
  async function deleteItem(itemId) {
    const res = await fetch(`https://react-http-e9374-default-rtdb.asia-southeast1.firebasedatabase.app/expense/${itemId}.json`, {
      method: 'DELETE',
      body: JSON.stringify(itemId),
      header: {
        'Content-Type': 'application/json'
      }
    })
    const data = await res.json()
    console.log(data)
  }
  const delItem = () => {
    deleteItem(selectedDeleteItem.id)
    setDeleteModal(false)
    setDeleteSuccess(true)
  }
  const reloadPage = () => { window.location.reload() }


  const onSetDeleteModalHandler = () => {
    setDeleteModal(true)
  }

  const onCloseModalHandler = () => { setDeleteModal(false) }


  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch('https://react-http-e9374-default-rtdb.asia-southeast1.firebasedatabase.app/expense.json')
        const data = await res.json()
        setData(data)
      } catch (e) {
        console.log(e)
      }
    }
    fetchData()
  }, [])
  let logArr = []

  for (let index in data) {
    logArr.push({
      id: index,
      detail: data[index].detail,
      title: data[index].category,
      value: +data[index].amount,
      type: data[index].type,
      date: data[index].date
    })

  }

  const selectList = ['All', 'Foods', 'Clothes', 'Alcohol', 'Coffee', 'Entertainment', 'Study', 'Transport', 'Salary', 'Part-Time', 'Reward']
    .map((el) => {
      return <option key={el} value={el}>{el}</option>
    })
  const selectMonth = ['All', 'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
    .map((el, index) => {
      return <option key={el} value={index}>{el}</option>
    })

  const onFilterMonthChangeHandler = (e) => { setFilterMonth(e.target.value) }


  const onFilterChangeHandler = (e) => { setFilterType(e.target.value) }

  let filteredLogArr = []

  if (filterType !== 'All') {
    filteredLogArr = logArr.filter(el => el.title === filterType)
  } else {
    filteredLogArr = logArr
  }

  let monthFilteredLogArr = []
  if (parseInt(filterMonth) !== 0) {
    monthFilteredLogArr = filteredLogArr.filter(el => new Date(el.date).getMonth() === filterMonth - 1)
  } else {
    monthFilteredLogArr = filteredLogArr
  }

  let detailModalProp = []
  const onCloseDetailModalHandler = () => { setDetailModal(false) }
  const onSetDetailModalHandler = (itemDetailArr) => {
    setDetailModal(true)
    setDetailProp(itemDetailArr)
  }

  const logData = monthFilteredLogArr.map(el => {
    let color;
    if (el.type === 'Income') { color = 'green' }
    if (el.type === 'Expense') { color = 'red' }
    const tableRowProp = {
      id: el.id,
      color: color,
      title: el.title,
      detail: el.detail,
      value: el.value,
      onSetDeleteModal: onSetDeleteModalHandler,
      onSetSelectedDeleteItem: onSetSelectedDeleteItem,
      onSetDetailModal: onSetDetailModalHandler,
      date: el.date,
      type: el.type
    }
    return <TableRow {...tableRowProp} />
  })

  useEffect(() => {
    if (logData.length > 0) { setIsLogEmpty(false) }
    else { setIsLogEmpty(true) }
  }, [logData])

  const deleteModalProp = {
    header: 'Are You Sure ?',
    modalMessage: `Press Okay To Delete ${selectedDeleteItem.detail}`
  }
  const deleteSuccessProp = {
    header: 'Success !',
    modalMessage: `This ${selectedDeleteItem.detail} Has Been Deleted.`
  }

  return (
    <>
      {deleteSuccess && <ModalOverlay {...deleteSuccessProp} onClickButton={reloadPage} />}
      {detailModal && <DetailModal {...detailProp} onCloseModal={onCloseDetailModalHandler} />}
      {deleteModal && <ModalOverlay onCloseModal={onCloseModalHandler} {...deleteModalProp} onClickButton={delItem} />}
      <Card className={classes.Log}>
        <h2>Transaction</h2>
        <select value={filterType} onChange={onFilterChangeHandler}>{selectList}</select>
        <select value={filterMonth} onChange={onFilterMonthChangeHandler}>{selectMonth}</select>
        <table>
          <thead>
            <tr>
              <th>Category</th>
              <th colSpan={2}>Detail</th>
              <th>Amount</th>
              <th>Delete</th>
            </tr>
          </thead>
          <tbody>
            {logData}
          </tbody>
        </table >
        {isLogEmpty && <p>No Transaction Found</p>}
      </Card >
    </>
  )
}

export default Log