import ModalOverlay from "../Modal/ModalOverlay"

const TableRow = (props) => {
  const clickDelete = () => {
    props.onSetDeleteModal()
    props.onSetSelectedDeleteItem({ id: props.id, detail: props.detail })
  }
  const clickDetail = () => {
    props.onSetDetailModal(props)
  }

  return (
    <tr key={props.id} style={{ color: props.color }}
    >
      <td>{props.title}</td>
      <td colSpan={2} onClick={clickDetail}>{props.detail}</td>
      <td>{props.value}</td>
      <td onClick={clickDelete}>Delete</td>
    </tr >
  )

}

export default TableRow 